.input-wrapper {
  width: 100%;
  min-height: calc(100vh - 119px);
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(255, 250, 250);
}

.input-content-wrapper {
  background-color: rgb(255, 250, 250);
  width: 800px;
  min-height: 200px;
  padding: 72px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 56px;
}

.input-wrapper h2 {
  font-size: 48px;
  font-weight: 800;
}

.custom-upload-btn {
  padding: 16px 16px;
  border: 2px solid rgb(65, 57, 57);
  border-radius: 4px;
  box-shadow: 3px 3px rgb(65, 57, 57);
  background-color: rgb(255, 179, 0);
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: rgb(65, 57, 57);
  font-size: 18px;
  font-weight: 700;
}

.custom-upload-btn:hover {
  background-color: rgb(254, 208, 103);
  box-shadow: 4px 4px rgb(65, 57, 57);
  cursor: pointer;
}

.custom-upload-btn:active {
  box-shadow: 2px 2px rgb(65, 57, 57);
  background-color: rgb(255, 179, 0);
}

@media (max-width: 800px) {
  .input-content-wrapper {
    width: 100%;
    height: calc(100vh - 77px);
    box-shadow: none;
    border-radius: 0;
    justify-content: center;
    padding: 8px;
    gap: 24px;
  }

  .input-wrapper h2 {
    font-size: 36px;
    font-weight: 700;
  }

  .custom-upload-btn {
    padding: 8px;
    font-size: 16px;
  }
}